import React,{ useRef, useEffect } from 'react'
import "./ContentTop.css"
import gsap from 'gsap';
import { ScrollTrigger } from 'gsap/ScrollTrigger';
import Magnetic from '../../../Components/Magnetic';

gsap.registerPlugin(ScrollTrigger);


// "https://cdn.dribbble.com/userupload/3536417/file/original-94d5ef0f3c8fdcc1d8c999ce90e9b8d3.jpg?resize=640x480&vertical=center"

const SomePorto = (props) => {
  return(
    <div className={`box-porto porto ${props.numberOfPorto}`}>
      <div className='photo-porto'>
        <div style={{backgroundColor: 'green', backgroundImage: `url(${ props.photo })`}}>

        </div>
      </div>

      <h2>{ props.nameOfPorto }</h2>
      <div className='d-flex wrap-info'>
        <span>{ props.type }</span>
        <span className='ms-auto'>{ props.createdAt }</span>
      </div>

    </div>
  )
}

const ContentTop = () => {
    const aboutMe = useRef(null);
    const morePorto = useRef(null);

    useEffect(() => {
        if (window.innerWidth > 1025 && aboutMe.current && morePorto.current) {
          gsap.fromTo(aboutMe.current, 
              {
                x: 0,
              },
              {
                x: -200,
                duration: 8,
                scrollTrigger: {
                    trigger: aboutMe.current,
                    start: 'top 100%',
                    end: 'top 0%',
                    scrub: 4,
                    toggleActions: 'restart none none none',
                }
              }
          );  
          
          gsap.fromTo(morePorto.current, 
            {
              x: -200,
            },
            {
              x: 0,
              duration: 8,
              scrollTrigger: {
                  trigger: morePorto.current,
                  start: 'top 100%',
                  end: 'top 0%',
                  scrub: 4,
                  toggleActions: 'restart none none none',
              }
            }
        ); 
        }

    },[]);

    return (
      <div className='container overflow-hidden'>
        <div className='wrap-title w-100 d-flex justify-content-evenly'>
          <p className='m-0 text-black title d-block'>
            Empowering brands to shine in the digital age. 
            Together we will establish the new norm. 
            No frills, always at the forefront.
          </p>

          <p className='body text-black d-block'>
            The fusion of my enthusiasm for  
            coding, and interaction places me in a 
            distinctive position in the realm of 
            web developer.
          </p>
        </div>

        <div ref={aboutMe} className='wrap-circle-about-me position-relative'>
          <div className='line-about-me position-absolute'></div>
          <Magnetic>
            <a href='awd' className='circle-about-me position-absolute m-0 text-white rounded-circle'>
                About me
            </a>
          </Magnetic>
        </div>

        <div className='wrap-selected-porto'>
          <p className='title text-black'>Selected porto</p>

          <div className='d-flex flex-wrap text-black m-0 justify-content-between'>

            <SomePorto 
              numberOfPorto={'porto-1'}
              photo={'https://cdn.dribbble.com/userupload/3536417/file/original-94d5ef0f3c8fdcc1d8c999ce90e9b8d3.jpg?resize=640x480&vertical=center'}
              nameOfPorto={'Download games'}
              type={'Website app'}
              createdAt={'2022'}
            />

            <SomePorto 
              numberOfPorto={'porto-2'}
              photo={'https://cdn.dribbble.com/users/14268/screenshots/5314733/media/5f686543c38d9b8143d5fde5f947babd.png?resize=640x480&vertical=center'}
              nameOfPorto={'Website product'}
              type={'Website app'}
              createdAt={'2022'}
            />

            <SomePorto 
              numberOfPorto={'porto-3'}
              photo={'https://cdn.dribbble.com/userupload/4001378/file/original-8d59f0fbd181faf43198734c2046a4c3.png?resize=640x480&vertical=center'}
              nameOfPorto={'Company profile'}
              type={'Website app'}
              createdAt={'2022'}
            />

            <SomePorto 
              numberOfPorto={'porto-4'}
              photo={'https://cdn.dribbble.com/userupload/10894828/file/original-2d7c7aa1c6271df85b52a418674e1f76.png?resize=640x480&vertical=center'}
              nameOfPorto={'Membership'}
              type={'Website app'}
              createdAt={'2022'}
            />
          </div>

              <div className='wrap-more-porto d-flex justify-content-center position-relative'>
                  <Magnetic>
                  <a href='awd' className='more-porto position-absolute m-0 text-black rounded-5'>
                      More porto
                  </a>
                </Magnetic>
              </div>
          

        </div>
      </div>
    )
}

export default ContentTop